.setting-item-switch-wrapper {
  display: flex;
  flex-direction: row;

  .setting-item-content {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-right: 16px;

    .setting-item-title {
      color: var(--text-primary-color);
      font-size: 14px;
    }

    .setting-item-description {
      color: var(--text-secondary-color);
      font-size: 12px;
      margin-top: 8px;
    }
  }
}
