@use '../mixins/button';
@use '../mixins/common';
@use '../mixins/input';

.numeric-input-wrapper {
  display: flex;
  align-items: center;
  @include common.gap(0, 8px);

  > .button-change-value {
    @include button.non-style-button();

    width: 20px;
    height: 20px;
  }

  > .input-number {
    @include input.non-style-input();

    font-size: 14px;
    color: var(--text-primary-color);
    text-align: center;
    margin: 0;
    width: 50px;
    border: 1px solid var(--border-color);
    border-radius: 4px;
  }
}
