.launch-window {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;

  .carousel-region {
    display: flex;
    flex-direction: column;
    width: 376px;
    background-color: var(--carousel-background-color);

    .img-logo {
      width: 80px;
      height: 23px;
      margin-top: 16px;
      margin-left: 24px;
      margin-bottom: 60px;
      user-select: none;
      -webkit-user-drag: none;
    }
  }

  .view-region {
    height: 100%;
    flex: 1;
    background-color: var(--background-color);
  }
}
