.two-step-verification-dialog {
  display: flex;
  flex-direction: column;
  background: var(--background-color);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  box-shadow: 0px 25px 30px rgba(0, 0, 0, 0.28);
  width: 420px;
  padding: 24px;

  .message-text {
    font-size: 14px;
    color: var(--text-primary-color);
  }
}
