@use '../../mixins/common';

.sync-state-popup-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 4px;

  > .sync-state-popup-content {
    margin: 0 8px;
    border: 1px solid var(--border-color);
    border-radius: 4px;
    padding: 8px;
    background-color: var(--background-color);
    max-height: 360px;
    box-shadow: var(--box-shadow);

    overflow: hidden auto;
    scrollbar-width: thin;
    scrollbar-color: var(--scroll-thumb-color) var(--background-color);

    .display-file-item {
      display: grid;
      grid-template-columns: auto 8px auto 1fr auto;
      grid-template-rows: auto 8px auto;
      @include common.gap(0, 8px);
      height: 62px;
      padding: 10px 8px;
      box-sizing: border-box;
      border-radius: 4px;
      line-height: normal;

      &:hover {
        background-color: var(--clickable-hover-color);
      }

      > img.file-icon {
        grid-row: 1 / 4;
        grid-column: 1 / 2;

        align-self: center;
        width: 32px;
        height: 32px;
      }

      > .text.file-name {
        grid-row: 1 / 2;
        grid-column: 3 / 4;

        font-size: 12px;
        line-height: normal;
        flex: 1;
      }

      > .text.bottom {
        grid-row: 3 / 4;
        grid-column: 3 / 4;
      }

      > .text.right-top {
        grid-row: 1 / 2;
        grid-column: 5 / 6;

        text-align: right;
      }

      > .text.right-bottom {
        grid-row: 3 / 4;
        grid-column: 5 / 6;

        text-align: right;
      }
    }
  }

  > .sync-state-popup-triangle {
    margin-top: -1px;
    z-index: 1;
    align-self: center;
  }
}
