.tooltip-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.tooltip-text {
  display: none;
  position: absolute;
  padding: 6px 8px;
  color: #fff;
  background-color: #555;
  border-radius: 4px;
  z-index: 100;
  box-sizing: border-box;
  font-size: 12px;
  word-break: break-all;

  &.active {
    display: inline-block;
  }
}
