.new-user-wizard-card {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  box-sizing: border-box;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  height: 112px;
  cursor: pointer;
  padding-left: 24px;
  padding-right: 16px;
  user-select: none;

  > .gap {
    flex: 1;
    min-width: 24px;
  }

  .title {
    color: var(--accent-color);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }

  .description {
    color: var(--text-primary-color);
    font-size: 12px;
    line-height: 16px;
    font-weight: 400;
  }

  .tip {
    display: block;
    color: var(--accent-color);
    font-size: 12px;
    line-height: 16px;
    font-weight: 400;
    min-width: 86px;
    justify-self: flex-end;
    text-align: right;
  }

  &:hover {
    border: 1px solid var(--accent-color);
    background: #419cff14;
  }
}
