@use '../mixins/input';

.input-wrapper {
  display: flex;
  position: relative;
  flex-direction: row;
  height: 40px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  outline: none;

  > .error-text {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: absolute;
    top: 46px;
    color: var(--error-color);
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    height: 18px;
    user-select: none;
    -webkit-user-drag: none;
  }

  &.focused,
  &:hover {
    border: 1px solid var(--accent-color);
  }

  &.error {
    border: 1px solid var(--error-color);
  }

  &.disabled {
    border: 1px solid var(--input-border-color-disabled);
    background: var(--input-background-color-disabled);

    input::placeholder {
      color: var(--input-color-disabled);
    }
  }

  input {
    @include input.non-style-input();
  }
}

input.text-block {
  @include input.non-style-input();
  width: auto;
  height: auto;
  font-weight: 400;
}
