.check-switch-wrapper {
  $check-switch-width: 40px;
  $check-switch-height: 20px;
  $check-switch-thumb-size: 12px;

  display: flex;
  width: $check-switch-width;
  height: $check-switch-height;
  border-radius: $check-switch-height / 2;
  background-color: var(--check-box-disable-background-color);
  cursor: pointer;
  transition: background-color 0.2s;

  > .check-switch-thumb {
    width: $check-switch-thumb-size;
    height: $check-switch-thumb-size;
    border-radius: $check-switch-thumb-size / 2;
    background-color: white;
    margin: ($check-switch-height - $check-switch-thumb-size) / 2;
    transition: margin-left 0.2s;
  }

  &.checked {
    background-color: var(--accent-color);

    > .check-switch-thumb {
      margin-left: $check-switch-width - ($check-switch-height + $check-switch-thumb-size) / 2;
    }
  }
}
