@use '../mixins/button';

.dropdown-button {
  @include button.non-style-button();

  position: relative;
  display: flex;
  align-items: center;
  justify-items: center;
  width: 32px;
  height: 32px;
  border-radius: 4px;

  &:hover {
    background-color: var(--clickable-hover-color);
  }

  &.active {
    background-color: var(--clickable-hover-color);
  }

  > .dropdown-icon {
    width: 20px;
    height: 20px;
  }
}
