.recent-view-wrapper {
  display: grid;
  grid-template-rows: auto 16px 1fr;
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .search-box-region {
    grid-row: 1 / 2;
    padding: 8px;
    border-bottom: 1px solid var(--split-line-color);

    > .combo-box {
      border-width: 0;
      height: 32px;

      > .text-box {
        line-height: 32px;
      }

      &.active {
        background-color: var(--clickable-hover-color);
      }
    }
  }

  .recent-view-region {
    grid-row: 3 / 4;
  }

  .recent-list-box-item-wrapper {
    display: grid;
    grid-template-rows: auto 4px auto;
    grid-template-columns: auto 16px auto 1fr auto;
    box-sizing: border-box;
    padding: 10px 16px;
    margin: 0 16px 0 8px;
    border-radius: 4px;
    transition: background-color 0.1s;

    &:hover {
      background-color: var(--clickable-hover-color);
    }

    > .text-message {
      grid-row: 1 / 2;
      grid-column: 1 / 6;
      color: var(--text-primary-color);
      font-size: 14px;
      line-height: 20px;
      cursor: pointer;

      &.deleted {
        text-decoration: line-through;
        cursor: default;
        pointer-events: none;
      }
    }

    > .operation-tag {
      $operations: 'unknown' black, 'add' #06d08d, 'edit' #ef8d1a, 'rename' #419cff, 'move' #419cff,
        'copy' #419cff, 'delete' #f56c6c, 'restore' #06d08d, 'purge' #f56c6c, 'lock' #ef8d1a,
        'unlock' #ef8d1a;

      grid-row: 3 / 4;
      grid-column: 1 / 2;
      border-radius: 4px;
      height: 24px;
      line-height: 24px;
      padding: 0 8px;
      font-size: 12px;
      align-self: center;

      @each $operation in $operations {
        &.#{nth($operation, 1)} {
          color: nth($operation, 2);
          background-color: change-color($color: nth($operation, 2), $alpha: 0.12);
        }
      }
    }

    > .text-editor {
      grid-row: 3 / 4;
      grid-column: 3 / 4;

      color: #a9a9a9;
      font-size: 12px;
      align-self: center;
    }

    > .text-time {
      grid-row: 3 / 4;
      grid-column: 5 / 6;

      color: var(--text-secondary-color);
      font-size: 12px;
      align-self: center;
    }
  }
}
