@use '../../mixins/common';

$content-max-width: 650px;

.app-recommend-view-wrapper {
  height: 100%;

  .app-recommend-content {
    display: grid;
    grid-template-rows: auto 1fr;
    margin: 0 16px 0 8px;
    justify-content: center;
  }

  .app-recommend-banner {
    position: relative;
    grid-row: 1 / 2;
    height: 96px;
    max-width: $content-max-width;

    .carousel {


      .slide-dot-list {
        margin-top: -16px;

        > button.slide-dot {
          width: 6px;
          height: 6px;
        }
      }
    }

    .banner-image {
      $banner-margin: 8px;

      position: absolute;
      width: calc(100% - 2 * #{$banner-margin});
      height: 80px;
      border-radius: 10px;
      box-shadow: 0 0 10px #00000050;
      margin: $banner-margin;
      object-fit: cover;
      object-position: center;
    }
  }

  .app-recommend-list {
    grid-row: 2 / 3;
    display: flex;
    flex-direction: column;
    max-width: $content-max-width;
    height: 100%;
    list-style: none;
    padding: 0;
    margin: 0;

    .app-recommend-item {
      display: grid;
      grid-template-rows: auto auto 12px auto;
      grid-template-columns: auto 12px auto 1fr auto;
      padding: 16px 8px;
      box-sizing: border-box;
      border-bottom: 1px solid var(--border-color);

      img.app-card-icon {
        grid-row: 1 / 3;
        grid-column: 1 / 2;
        width: 48px;
        height: 48px;
      }

      .text-app-card-name {
        grid-row: 1 / 2;
        grid-column: 3 / 4;
        align-self: center;
        font-size: 14px;
        color: var(--text-primary-color);
      }

      .app-card-platforms {
        display: flex;
        flex-direction: row;
        grid-row: 2 / 3;
        grid-column: 3 / 4;
        align-self: center;
        @include common.gap(0, 8px);

        .text-app-card-platform {
          background-color: var(--clickable-hover-color);
          border-radius: 4px;
          padding: 2px 4px;
          box-sizing: border-box;
          font-size: 12px;
          color: var(--text-secondary-color);
        }
      }

      .app-card-button {
        grid-row: 1 / 3;
        grid-column: 5 / 6;
        align-self: center;
      }

      .text-app-card-description {
        grid-row: 4 / 5;
        grid-column: 1 / 6;
        font-size: 12px;
        color: var(--text-primary-color);
        line-height: 1.5em;
      }
    }
  }
}
