.check-box {
  display: flex;
  flex-direction: row;

  input[type='checkbox'] {
    width: 14px;
    height: 14px;
    margin: 0;
    align-self: center;
    cursor: pointer;
    outline: none;

    + label {
      color: var(--text-primary-color);
      font-size: 14px;
      align-self: center;
      margin-left: 8px;
      cursor: pointer;
      user-select: none;
    }

    &[disabled] {
      cursor: default;
    }
  }
}
