@use '../mixins/common';
@use '../mixins/button';

.hamburg-tab-control-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  width: 100%;
  height: 100%;

  .hamburg-mask {
    flex: 0;
  }

  &.expanded > .hamburg-view.layer-1 > .hamburg-sidebar .hamburg-sidebar-addon button {
    margin: 0 6px;
    transition: margin 0.2s;
  }

  &.overlay.expanded {
    .hamburg-mask {
      background-color: #00000080;
      flex: 1;
      pointer-events: visible;
    }

    .hamburg-content {
      filter: blur(2px);
    }

    .hamburg-sidebar {
      transition: width 0.1s cubic-bezier(0, 0.8, 0.55, 0.85);
    }
  }

  > .hamburg-view {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-row: 1 / 2;
    grid-column: 1 / 2;

    &.layer-0 {
      > .hamburg-content {
        display: grid;
        grid-template-rows: 1fr auto;

        .hamburg-switchable-item {
          display: none;
          width: 100%;
          height: 100%;

          &.selected {
            display: block;
          }
        }
      }
    }

    &.layer-1 {
      z-index: 1;
      pointer-events: none;

      > .hamburg-sidebar {
        display: grid;
        grid-template-rows: auto auto 1fr auto;
        padding: 8px;
        box-sizing: border-box;
        background-color: var(--background-color);
        border-right: 1px solid var(--split-line-color);
        pointer-events: visible;
        transition: width 0.1s cubic-bezier(0, 0.8, 0.55, 0.85);

        > .hamburg-button {
          @include button.flatten-button(40px, 40px);
        }

        > .hamburg-tab-headers {
          margin-top: 8px;
          display: flex;
          flex-direction: column;
          @include common.gap(8px, 0);
        }

        > .hamburg-sidebar-addon {
          grid-row: 4 / 5;

          .sidebar-button {
            @include button.flatten-button(40px, 40px);
            margin: 4px 0;
            transition: margin 0.2s;

            &:hover {
              background-color: var(--clickable-hover-color);
            }

            &.selected {
              background-color: var(--tab-header-selected-color);
            }
          }

          .setting-icon-container {
            display: flex;
            justify-content: right;
            align-items: center;
          }

          .setting-icon-red-dot {
            margin-left: 4px;
            margin-right: -4px;
            min-width: 4px;
            width: 4px;
            height: 4px;
            border-radius: 50%;            
            background-color: var(--error-color);
          }
        }
      }
    }
  }
}

.hamburg-tab-header-item {
  display: grid;
  grid-template-columns: 40px auto;
  align-items: center;

  height: 40px;
  box-sizing: border-box;
  border-radius: 4px;
  user-select: none;
  cursor: pointer;

  > .hamburg-tab-header-icon {
    width: 20px;
    height: 20px;
    justify-self: center;
  }

  .change-count-dot {
    margin-left: -6px;
    min-width: 4px;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: var(--error-color);
  }

  &:hover {
    background-color: var(--clickable-hover-color);
  }

  &.selected {
    background-color: var(--tab-header-selected-color);
  }
}

.hamburg-tab-control-wrapper.expanded .hamburg-tab-header-item {
  grid-template-columns: 40px 12px auto 1fr auto;

  > span.hamburg-tab-header-title {
    grid-column: 3 / 4;
    font-size: 14px;
    color: var(--text-primary-color);
    cursor: pointer;
  }

  > .change-count-badge {
    grid-column: 5 / 6;

    min-width: 20px;
    height: 20px;
    line-height: 20px;
    border-radius: 10px;
    background-color: var(--error-color);
    font-size: 12px;
    color: white;
    padding: 0 6px;
    box-sizing: border-box;
    margin-right: 8px;
  }
}
