.yiyang-install-window {
  width: 100vw;
  height: 100vh;

  .banner-img {
    display: flex;
    justify-content: center;
    img {
      height: 492px;
    }
  }

  .buttons,
  .install-status {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 108px;
  }

  .buttons {
    .install {
      width: 320px;
      height: 48px;
      background: var(--accent-color);
      box-shadow: 0px 2px 8px 0px rgba(32, 145, 249, 0.4);
      border-radius: 8px;
      text-align: center;
      line-height: 48px;
      color: #fff;
      cursor: pointer;
    }
  }

  .install-status {
    .download-progress,
    .installing {
      border-radius: 8px;
      width: 704px;
      height: 8px;
      background-color: var(--border-color);
    }

    .download-progress {
      .progress-value {
        height: 100%;
        background: linear-gradient(62deg, #52d9ff 0%, #80a3ff 100%);
        border-radius: 8px;

        transition: width 0.3s ease-in-out;
      }
    }

    .installing {
      overflow: hidden;
      &-bar {
        width: 160px;
        height: 100%;
        background: linear-gradient(62deg, #52d9ff 0%, #80a3ff 100%);
        border-radius: 8px;
        animation: installing 3s linear 0s infinite;
      }
    }

    .status-text {
      color: var(--text-primary-color);
      font-size: 14px;
      line-height: 20px;

      span {
        color: var(--accent-color);
        cursor: pointer;
      }
    }
  }
}

@keyframes installing {
  from {
    margin-left: -160px;
  }
  to {
    margin-left: 864px;
  }
}
