@use '../mixins/button';
@use '../mixins/input';

.search-box-wrapper {
  display: grid;
  align-items: center;
  grid-template-columns: auto 8px 1fr 8px auto;
  height: 48px;
  padding: 0 12px 0 16px;

  > img.search-icon {
    grid-column: 1 / 2;
    width: 20px;
    height: 20px;
  }

  > input.search-input {
    grid-column: 3 / 4;
    @include input.non-style-input();
    margin: 0;
  }

  > .search-addon-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-column: 5 / 6;

    .search-addon {
      grid-row: 1 / 2;
      grid-column: 1 / 2;
    }

    .search-close {
      grid-row: 1 / 2;
      grid-column: 1 / 2;
      justify-self: right;

      @include button.flatten-button(32px, 32px);
    }

    .float-in {
      $play-time-span: 400ms;

      &-enter {
        visibility: hidden;
        transform: translateX(100%);
      }

      &-enter-active {
        visibility: visible;
        transform: translateX(0%);
        transition: all $play-time-span/2 $play-time-span/2;
      }

      &-exit {
        transform: translateX(0%);
      }

      &-exit-active {
        animation: identifier $play-time-span/2 forwards;

        @keyframes identifier {
          from {
            transform: translateX(0%);
          }

          to {
            transform: translateX(100%);
            visibility: hidden;
          }
        }
      }
    }
  }
}
