.data-grid-wrapper {
  overflow-x: hidden;
  overflow-y: auto;
  padding-right: 4px;

  table {
    border-collapse: collapse;
    width: 100%;

    > thead {
      > tr {
        border-bottom: 1px solid var(--border-color);
        user-select: none;

        > th {
          padding: 0;
          height: 32px;
          background: var(--background-color);
          text-align: left;
          position: sticky;
          top: 0;
          z-index: 100;
        }
      }
    }

    > tbody {
      > tr {
        height: 40px;
        border-bottom: 1px solid var(--border-color);
        user-select: none;

        > td {
          padding: 0;
        }
      }
    }
  }
}
