@use '../../mixins/button';

.bookmarks-view-wrapper {
  display: grid;
  grid-template-rows: auto 16px 1fr;
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .search-box-region {
    grid-row: 1 / 2;
    border-bottom: 1px solid var(--split-line-color);

    button.search-addon-button {
      @include button.flatten-button(32px, 32px);

      > img {
        width: 20px;
        height: 20px;
      }
    }
  }
  .main-content-region {
    grid-row: 3 / 4;
  }

  .bookmark-list-box-item {
    display: grid;
    grid-template-columns: auto 8px 1fr 16px auto;
    border-radius: 4px;
    height: 52px;
    align-items: center;
    padding: 0 16px;
    margin: 0 16px 0 8px;
    box-sizing: border-box;

    &:hover {
      background-color: var(--clickable-hover-color);

      .bookmark-button {
        visibility: visible;
      }
    }

    .bookmark-file-icon {
      grid-column: 1 / 2;
      width: 32px;
      height: 32px;
    }

    .text-bookmark-file-name {
      grid-column: 3 / 4;
      color: var(--text-primary-color);
      font-size: 14px;
    }

    .bookmark-button {
      grid-column: 5 / 6;
      @include button.non-style-button();
      visibility: hidden;

      > img {
        width: 20px;
        height: 20px;
      }
    }
  }
}
