@use '../../mixins/common';
@use '../../mixins/button';

.account-info-content {
  display: flex;
  flex-direction: column;
  @include common.gap(24px, 0);

  .property- {
    &key {
      font-size: 14px;
      color: var(--text-primary-color);
    }

    &value {
      font-size: 14px;
      color: var(--text-secondary-color);
    }
  }

  .property-item-wrapper {
    display: flex;
    flex-direction: column;
    @include common.gap(8px, 0);
  }

  .account-usage-region {
    display: flex;
    flex-direction: column;
    @include common.gap(8px, 0);

    .progress {
      width: 100%;
    }
  }

  .account-links {
    display: flex;
    flex-direction: column;
    @include common.gap(8px, 0);
    align-self: flex-end;
    align-items: flex-end;
  }
}

progress[value] {
  appearance: none;
  border: none; // Firefox
  background-color: var(--clickable-hover-color);
  border-radius: 3px;

  height: 6px;

  &::-webkit-progress-bar {
    background-color: var(--clickable-hover-color);
    border-radius: 3px;
  }

  &::-webkit-progress-value {
    background-color: var(--accent-color);
    border-radius: 3px;
  }

  &::-moz-progress-bar {
    background-color: var(--accent-color);
    border-radius: 3px;
  }
}
