@use '../../mixins/button';
@use '../../mixins/common';

.sync-folder-view-wrapper {
  display: grid;
  grid-template-rows: auto 16px 1fr;
  width: 100%;
  height: 100%;
  box-sizing: border-box;

  .search-box-region {
    grid-row: 1 / 2;
    border-bottom: 1px solid var(--split-line-color);

    .search-box-addon-list {
      display: flex;
      flex-direction: row;
      @include common.gap(0, 8px);
    }
  }

  .folder-view-region {
    grid-row: 3 / 4;
  }

  .folder-item-icon {
    grid-row: 1 / 4;
    grid-column: 1 / 2;

    display: flex;
    flex-direction: row;
    align-items: flex-end;
    width: 37px;

    > .folder-icon {
      width: 32px;
      height: 30px;
    }
    > .folder-state-icon {
      width: 14px;
      height: 14px;
      margin-left: -7px;
    }
  }

  .label-folder-group-name {
    margin-left: 16px;
  }

  .sync-folder-group-pinned {
    display: flex;
    flex-wrap: wrap;
    margin: 8px 16px 16px 16px;
  }

  .sync-folder-group-unpinned {
    display: flex;
    flex-direction: column;
    margin: 8px 0 0 0;
  }

  .pinned-folder-list-item-wrapper {
    @include button.non-style-button();

    display: flex;
    flex-direction: column;
    @include common.gap(6px, 0);
    align-items: center;
    width: 80px;
    height: 72px;
    margin: 2px;
    padding: 10px 4px;
    box-sizing: border-box;
    border-radius: 4px;

    &:hover {
      background-color: var(--clickable-hover-color);
    }

    .folder-name-text {
      font-size: 12px;
      color: var(--text-primary-color);
      width: 72px;
      cursor: pointer;
      text-align: center;
    }
  }

  .folder-list-item-wrapper {
    display: grid;
    grid-template-rows: auto 6px auto;
    grid-template-columns: auto 8px 1fr 8px auto;
    align-items: center;
    height: 62px;
    box-sizing: border-box;

    padding: 10px 16px;
    margin: 0 16px 0 8px;
    border-radius: 4px;
    transition: background-color 0.1s;

    &:hover,
    &.active {
      background-color: var(--clickable-hover-color);

      .folder-item-buttons {
        visibility: visible;
      }
    }

    .folder-name-text {
      grid-row: 1 / 2;
      grid-column: 3 / 4;

      font-size: 14px;
      color: var(--text-primary-color);
      cursor: pointer;
    }

    .folder-permissions-text {
      grid-row: 3 / 4;
      grid-column: 3 / 4;

      font-size: 12px;
      color: #a9a9a9;
      cursor: default;
    }

    .folder-item-buttons {
      grid-row: 1 / 4;
      grid-column: 5 / 6;

      visibility: hidden;
      display: flex;
      flex-direction: row;
      @include common.gap(0, 16px);

      .folder-item-button {
        @include button.non-style-button();
      }
    }
  }
}

.sync-folder-search-result-wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;

  .search-history-view {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-template-rows: auto 8px 1fr;
    height: 100%;

    .label-search-history {
      grid-area: 1 / 1 / 2 / 2;
      margin-left: 8px;
    }

    .button-clear-history {
      grid-area: 1 / 3 / 2 / 4;
      font-size: 12px;
      margin-right: 16px;
    }

    .search-history-list {
      grid-area: 3 / 1 / 4 / 4;
      list-style: none;
      margin: 0;
      padding: 0;

      display: flex;
      flex-direction: column;

      .search-history-item {
        display: grid;
        grid-template-columns: auto 8px 1fr 16px auto;
        align-items: center;
        padding: 10px 16px;
        margin: 0 16px 0 8px;
        border-radius: 4px;

        &:hover {
          background-color: var(--clickable-hover-color);
        }

        .history-text {
          grid-column: 3 / 4;
        }

        .history-close-button {
          grid-column: 5 / 6;
          @include button.non-style-button();

          width: 14px;
          height: 14px;
        }
      }
    }
  }

  .label-group-title {
    display: inline-block;
    margin-left: 16px;
    margin-bottom: 8px;
  }

  .label-search-tip {
    display: block;
    color: var(--accent-color);
    background-color: var(--background-color);
    font-size: 12px;
    font-weight: 400;
    line-height: 14px;
    margin-left: 6px;
    margin-bottom: 8px;
  }

  .search-result-list {
    list-style: none;
    margin: 0;
    padding: 0;

    &.folder {
      margin-bottom: 16px;
    }
  }

  .search-result-item {
    display: grid;
    align-items: center;
    border-radius: 4px;
    padding: 10px 16px;
    margin: 0 16px 0 8px;
    box-sizing: border-box;
    height: 62px;
    cursor: pointer;

    &:hover {
      background-color: var(--clickable-hover-color);
    }

    &.folder {
      grid-template-columns: auto 8px 1fr;
      grid-template-rows: auto 6px auto;

      .folder-icon {
        grid-area: 1 / 1 / 4 / 2;
        align-self: center;
        justify-self: center;
      }

      .label-title {
        grid-area: 1 / 3 / 2 / 4;
      }

      .label-subtitle {
        grid-area: 3 / 3 / 4 / 4;
      }
    }

    &.file {
      grid-template-rows: auto 6px auto;

      .label-title {
        grid-row: 1 / 2;
      }

      .label-subtitle {
        grid-row: 3 / 4;
      }
    }
  }
}
