.general-pre-window-view {
  display: flex;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
  padding: 40px 24px 24px 24px;

  > .error-text {
    color: var(--error-color);
    font-size: 12px;
    font-weight: 400;
    line-height: 17px;
    height: 17px;
    align-self: center;
    margin: 8px 0;
    user-select: none;
  }

  .buttons {
    display: flex;
    flex-direction: row;

    > .gap {
      width: 32px;
    }
  }

  > .gap {
    flex: 1;
  }

  .footer {
    align-self: flex-end;
  }
}
