@use '../../mixins/common';
@use '../../mixins/button';

.settings-view {
  .split-line {
    border-top: 1px solid var(--split-line-color);
  }

  .setting-feature-upgrade-red-dot {
    margin-left: -6px;
    min-width: 4px;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: var(--error-color);
  }

  .setting-item-combobox {
    display: grid;
    grid-template-columns: auto 1fr auto;
    align-items: center;

    .setting-item-title {
      color: var(--text-primary-color);
      font-size: 14px;
    }

    .combo-box {
      grid-column: 3 / 4;
      height: 32px;

      .text-box {
        line-height: 32px;
        white-space: nowrap;
      }
    }
  }

  &.content {
    &-general {
      display: flex;
      flex-direction: column;
      @include common.gap(24px, 0);
    }

    &-network {
      display: flex;
      flex-direction: column;
      @include common.gap(24px, 0);

      .network-proxy-type-item {
        display: grid;
        grid-template-columns: auto 1fr auto;
        align-items: center;

        .label-proxy-type {
          grid-column: 1 / 2;
        }

        .combo-box {
          grid-column: 3 / 4;
          width: 120px;
        }
      }

      .network-proxy-info {
        display: grid;
        grid-template-columns: 1fr 8px 1fr;
        grid-template-rows: auto 24px auto 16px auto;

        .input-proxy-host {
          grid-area: 1 / 1 / 2 / 2;
        }

        .input-proxy-port {
          grid-area: 1 / 3 / 1 / 4;
        }

        .label-proxy-authentication {
          grid-area: 3 / 1 / 4 / 4;
        }

        .input-proxy-account {
          grid-area: 5 / 1 / 6 / 2;
        }

        .input-proxy-password {
          grid-area: 5 / 3 / 6 / 4;
        }
      }
    }

    &-storage {
      display: grid;
      align-items: center;
      grid-template-rows: auto 24px auto 24px auto 16px auto 24px auto;
      grid-template-columns: auto 1fr auto;

      > .label-description {
        grid-area: 1 / 1 / 2 / 5;
      }

      > .label-current-usage {
        grid-area: 3 / 1 / 4 / 2;
      }

      > .label-max-usage {
        grid-area: 5 / 1 / 6 / 2;
      }

      > .current-usage-value {
        grid-area: 3 / 3 / 4 /4;
        justify-self: end;
        display: flex;
        @include common.gap(0, 16px);

        > .button-current-usage-value {
          @include button.non-style-button();
          width: 20px;
          height: 20px;
        }
      }

      > .label-max-usage-value {
        grid-area: 5 / 3 / 6 / 4;
        justify-self: end;
      }

      > .slider-bar-max-usage {
        grid-area: 7 / 1 / 8 / 4;
      }

      > .label-cleaning-cycle {
        grid-area: 9 / 1 / 10 / 2;
      }

      > .numeric-input-cleaning-cycle {
        grid-area: 9 / 3 / 10 / 4;
        justify-self: end;
      }
    }

    &-about {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &.footer {
    &-general {
      display: flex;
      flex-direction: column;
    }

    &-network {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 40px;
      @include common.gap(0, 8px);

      button {
        width: 88px;
      }
    }

    &-storage {
      display: flex;
      justify-content: flex-end;
      margin-bottom: 40px;
      @include common.gap(0, 8px);

      button {
        width: 88px;
      }
    }

    &-about {
      display: flex;
      flex-direction: row;
      @include common.gap(0, 16px);
      margin-bottom: 24px;
      justify-content: center;
    }
  }
}
