$border-width: 1px;

.combo-box {
  position: relative;
  height: 40px;
  border: $border-width solid var(--border-color);
  border-radius: 4px;
  cursor: pointer;

  .text-box {
    color: var(--text-primary-color);
    font-size: 14px;
    line-height: 40px;
    padding: 0 16px;
    user-select: none;
  }

  .drop-down-box {
    display: none;
    position: absolute;
    z-index: 100;
    list-style: none;
    top: 46px;
    left: -$border-width;
    width: 100%;
    height: auto;
    overflow: hidden visible;
    border: $border-width solid var(--border-color);
    border-radius: 4px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.08);
    background-color: var(--background-color);

    span {
      color: var(--text-primary-color);
      font-size: 12px;
      line-height: 28px;
      cursor: pointer;
      user-select: none;
    }

    &.active {
      display: flex;
      flex-direction: column;
    }

    .drop-down-box-item {
      height: 28px;
      padding: 0 16px;
      white-space: nowrap;

      &:hover {
        background-color: var(--list-item-background-color-hover);
      }

      &.selected span {
        color: var(--accent-color);
      }
    }
  }
}
