.context-menu-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;

  &.active {
    pointer-events: visible;
  }
}

.context-menu-list {
  position: absolute;
  display: none;
  flex-direction: column;
  z-index: 1;

  width: 160px;
  border-radius: 4px;
  border: 1px solid var(--border-color);
  background-color: var(--background-color);
  padding: 4px 0;
  box-shadow: var(--box-shadow);

  &.active {
    display: flex;
  }

  > .menu-item {
    display: flex;
    align-items: center;
    height: 28px;
    padding: 0 16px;
    font-size: 12px;
    color: var(--text-primary-color);
    cursor: pointer;

    &:hover {
      background-color: var(--clickable-hover-color);
    }
  }

  > .split-line {
    border-top: 1px solid var(--split-line-color);
    margin: 4px;
  }
}
