@use '../../mixins/common';
@use '../../mixins/button';

.setting-view-template-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 100%;

  .setting-view-template-region {
    display: flex;
    flex-direction: column;
    flex: 1;
    max-width: 640px;

    > .header-region {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin: 0 16px 0 8px;
      border-bottom: 1px solid var(--split-line-color);
      min-height: 48px;

      > .text.header {
        height: 40px;
        line-height: 40px;
        font-size: 14px;
        color: var(--title-color);
        margin: 4px 8px;
      }
    }

    > .content-region {
      flex: 1;
      margin: 24px;
    }

    > .footer-region {
      margin: 0 16px 0 8px;
    }
  }
}

.settings-button {
  @include button.non-style-button();

  height: 40px;
  margin: 4px 0;
  border-radius: 4px;
  padding: 0 8px;
  width: 100%;

  > .button-text {
    font-size: 14px;
    color: var(--title-color);
    flex: 1;
    text-align: left;
    cursor: pointer;
  }

  &.header {
    .button-text {
      margin-left: 8px;
    }
  }

  &.footer {
    &:hover {
      background-color: var(--clickable-hover-color);
    }
  }
}
