body.theme-dark {
  --background-color: #333;
  --carousel-background-color: #464646;
  --title-color: #ffffff;
  --text-primary-color: #e8e8e8;
  --text-secondary-color: #9f9f9f;
  --border-color: #5e5e5e;
  --list-item-background-color-hover: #464646;
  --input-color-disabled: #696969ff;
  --input-border-color-disabled: #5e5e5eff;
  --input-background-color-disabled: #5e5e5e52;

  --split-line-color: #464646;
  --clickable-hover-color: #3c3c3c;
  --tab-header-selected-color: #464646;
  --scroll-thumb-color: #464646;
  --check-box-disable-background-color: #464646;
  --box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.24);
}
