.exception-window {
  display: flex;
  flex-direction: column;

  .top-panel {
    margin: 12px 20px 0 20px;
    display: flex;

    img {
      width: 64px;
      height: 64px;
    }

    .text {
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-around;

      margin-left: 16px;

      .title {
        font-size: 20px;
        color: var(--accent-color);
      }

      .desc {
        font-size: 12px;
        color: var(--title-color);
      }
    }
  }

  .content-panel {
    position: relative;
    margin: 20px;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    height: 284px;
    overflow: auto;

    .scanning {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      text-align: center;

      img {
        width: 64px;
        height: 64px;
      }

      &-text {
        margin-top: 10px;
        font-size: 12px;
        color: var(--text-primary-color);
      }
    }

    .exception-list {
      display: flex;
      flex-direction: column;
      padding: 8px 16px;
      width: calc(100vw - 72px);

      .top-bar,
      .exception {
        display: flex;
        align-items: center;
        height: 30px;
        text-align: left;

        input[type='checkbox'] {
          width: 24px;
        }

        .name,
        .reason,
        .path > span {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }

        .name {
          width: 30%;
          max-width: 30%;
        }

        .reason {
          flex: 1;
        }

        .path {
          display: flex;
          align-items: center;
          width: 30%;
          max-width: 30%;

          img {
            margin-right: 4px;
            width: 16px;
            height: 16px;
          }

          span {
            cursor: pointer;

            &:hover {
              text-decoration: underline;
            }
          }
        }
      }

      .top-bar {
        position: sticky;
        top: 0;
        width: 100%;
        font-size: 12px;
        color: var(--text-secondary-color);
      }

      .exception {
        color: var(--text-primary-color);
        font-size: 12px;

        .name {
          cursor: pointer;
        }

        &.selected {
          background: var(--tab-header-selected-color);
        }

        &:hover {
          background: var(--clickable-hover-color);
        }
      }

      .exception-group {
        display: flex;
        flex-direction: column;
        &-header {
          display: flex;
          align-items: center;
          height: 16px;
          font-size: 12px;
          color: var(--text-secondary-color);
          line-height: 16px;

          img {
            margin-right: 8px;
            width: 9px;
            height: 9px;
            transform: rotate(90deg);
            cursor: pointer;

            &.folded {
              transform: rotate(0);
            }
          }
        }
      }
    }
  }

  .bottom-panel {
    margin: 12px 20px 20px 20px;
    height: 16px;

    display: flex;
    justify-content: space-between;

    .why {
      font-size: 12px;
      color: #008dff;
      cursor: pointer;
    }

    .resolve {
      font-size: 12px;
      color: #008dff;
      cursor: pointer;
      user-select: none;

      &.disabled {
        color: var(--input-color-disabled);
        cursor: not-allowed;
      }
    }
  }
}
