.carousel {
  display: flex;
  flex-direction: column;
  user-select: none;
  -webkit-user-drag: none;

  .slide-list {
    position: relative;
    align-self: center;
    overflow: hidden;

    .slide-item {
      position: absolute;

      &.right-to-left {
        &-enter {
          transform: translateX(100%);
        }

        &-enter-active {
          transform: translateX(0);
          transition: transform 500ms;
        }

        &-exit {
          transform: translateX(0);
        }

        &-exit-active {
          transform: translateX(-100%);
          transition: transform 500ms;
        }
      }

      &.left-to-right {
        &-enter {
          transform: translateX(-100%);
        }

        &-enter-active {
          transform: translateX(0);
          transition: transform 500ms;
        }

        &-exit {
          transform: translateX(0);
        }

        &-exit-active {
          transform: translateX(100%);
          transition: transform 500ms;
        }
      }
    }
  }

  .slide-dot-list {
    display: flex;
    flex-direction: row;
    align-self: center;

    > button.slide-dot {
      width: 8px;
      height: 8px;
      padding: 0;
      border-radius: 4px;
      border-width: 0;
      background-color: #86bcff;
      opacity: 0.2;
      outline: none;
      cursor: pointer;
      margin: 0 5px;

      &.selected {
        opacity: 0.5;
      }
    }
  }
}
