$slider-thumb-size: 16px;

@mixin slider-thumb-style {
  appearance: none;
  width: $slider-thumb-size;
  height: $slider-thumb-size;
  border-radius: 50%;
  border: 4px solid var(--accent-color);
  box-sizing: border-box;
  background-color: white;
  cursor: pointer;
}

@mixin slider-track-style {
  height: 6px;
  border-radius: 3px;
  background-color: var(--border-color);
}

.slider-bar-wrapper {
  display: grid;
  grid-template-columns: 1fr $slider-thumb-size;

  input[type='range'].slider-bar {
    grid-row: 1 / 2;
    grid-column: 1 / 3;

    @include slider-track-style();
    appearance: none;
    width: 100%;
    outline: none;

    &::-webkit-slider-thumb {
      @include slider-thumb-style();
    }

    &::-moz-range-track {
      @include slider-track-style();
    }

    &::-moz-range-progress {
      @include slider-track-style();
      background-color: var(--accent-color);
    }

    &::-moz-range-thumb {
      @include slider-thumb-style();
    }
  }

  .slider-progress {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    height: 6px;
    border-radius: 3px 0 0 3px;
    background-color: var(--accent-color);
  }
}
