.sync-state-bar-wrapper {
  display: grid;
  grid-template-columns: auto 8px 1fr 8px auto;
  align-items: center;
  height: 48px;
  border-top: 1px solid var(--split-line-color);
  padding: 0 16px;
  position: relative;

  .sync-state-icon {
    grid-column: 1 / 2;
  }

  .sync-state-message {
    grid-column: 3 / 4;
  }

  .sync-state-button {
    grid-column: 5 / 6;
  }

  .sync-state-bar-popup {
    display: none;
    position: absolute;
    width: 100%;
    bottom: 100%;

    &:hover {
      display: block;
    }
  }

  &:hover .sync-state-bar-popup {
    display: block;
  }

  span {
    display: inline;
    font-size: 14px;
    line-height: 20px;
    color: var(--text-primary-color);
  }

  img {
    width: 20px;
    height: 20px;
  }

  button {
    background-color: transparent;
    border-radius: 4px;
    cursor: pointer;
    user-select: none;
    border-width: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;

    &:focus {
      outline: none;
    }
  }
}
